        *,
         ::after,
         ::before {
            padding: 0;
            margin: 0;
            box-sizing: border-box;
        }
        
        #root {
            width: 100%;
            height: 100vh;
            background-image: url("img/bgcover.avif");
            background-repeat: no-repeat;
            background-position: center;
        }
        
        #root>.container {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100vh;
        }
        
        .header-info {
            /* max-width: 350px; */
            /* height: 450px; */
            /* margin: 100px auto; */
            display: flex;
            flex-flow: column nowrap;
            background-color: white;
            transition: all .3s;
            border-radius: 10px;
            box-shadow: 0 0 0 8px rgb(255 255 255 / 20%);
            /* padding: 10px; */
        }
        
        .figure-header {
            min-height: 200px;
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
            position: relative;
            overflow: hidden;
            border-radius: 9px 9px 0 0;
        }
        
        .info-rat-head {
            display: flex;
            flex-flow: column wrap;
            width: 100%;
            height: auto;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 1;
        }
        
        .bg-cover {
            display: block;
            height: 160px;
            width: 100%;
            background: url(img/2.jpg);
            position: absolute;
            top: -13%;
            left: 0;
            z-index: 0;
            background-position: center;
            background-size: cover;
            filter: blur(6px);
            position: absolute;
            transform: scale(1.2);
            transition: 0.5s;
        }
        
        .logo-rat {
            margin-top: 20px;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            box-shadow: 0 8px 8px rgb(182 182 182 / 27%);
            object-position: center;
            /* position: absolute; */
            transition: all .5s;
        }
        
        .title-parent {
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            align-items: center;
            /* width: 100%; */
        }
        
        #title-name {
            margin: 8px 0;
            font-size: 20px;
            font-weight: 700;
            color: rgb(16 16 16);
            text-shadow: 0 0 11px #b9b9b9;
            transition: all .5s;
        }
        
        #sub-title {
            font-size: 15px;
            font-weight: 500;
            margin-top: 0;
            color: rgb(127 127 127);
            transition: all .5s;
        }
        
        .about-header {
            width: 100%;
            height: 100%;
            padding: 0 10px;
            margin: 0;
            line-height: 22px;
        }
        
        .about-header>p:nth-of-type(1) {
            font-weight: 600;
            font-size: 20px;
        }
        
        .about-me {
            padding-left: 18px;
            font-size: 15px;
        }
        
        .link-sush {
            font-size: 23px;
            display: flex;
            width: 45px;
            height: 45PX;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: #f1f1f1;
            color: #afbdd3;
            margin: 10px 10px;
            transition: all .5s;
        }
        
        .link-sush>i {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        .link-sush:hover {
            transform: scale(1.1);
            color: #847dff;
            box-shadow: 0 0 15px #5b4fce82;
        }
        /* nav */
        
        .nav-menu {
            margin-top: 5px;
            overflow: hidden;
        }
        
        .nav-menu>ul {
            width: 100%;
            display: flex;
            flex-flow: row nowrap;
            list-style: none;
            padding: 0;
            margin: 0;
            align-items: center;
            justify-content: space-around;
        }
        
        .nav-menu>ul>li {
            width: 100%;
            height: 100%;
            cursor: pointer;
            padding: 15px;
            margin: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            font-weight: 600;
            border-bottom: 3px solid transparent;
            transition: all .5s;
        }
        
        .nav-menu>ul>li:hover {
            background-color: rgb(198 214 255 / 65%);
        }
        
        .nav-menu>ul>li:nth-of-type(1) {
            border-radius: 0 0 0 9px;
            background-color: rgb(227 235 255 / 68%);
            border-bottom: 3px solid #847dff;
        }
        
        .nav-menu>ul>li:nth-of-type(3) {
            border-radius: 0 0 9px 0;
        }
        /* ex-cont */
        
        .info-rat-head-excont {
            flex-flow: row wrap;
            justify-content: start;
            height: 140px;
            margin-left: 20px;
            align-items: self-start;
            margin-bottom: 0;
            animation: animeinfo forwards linear 1s;
        }
        
        @keyframes animeinfo {
            0% {
                opacity: .5;
            }
            100% {
                opacity: 1;
            }
        }
        
        .logo-rat-excont {
            width: 80px;
            height: 80px;
            margin-top: 15px;
        }
        
        .title-parent-excont {
            margin-left: 15px;
            margin-top: 6px;
        }
        /* contact-header */
        
        .contact-header {
            transition: all 1s;
            display: flex;
            flex-flow: row nowrap;
            width: 100%;
            padding: 10px;
            margin-bottom: 10px;
            align-items: center;
            justify-content: flex-start;
        }
        
        .contact-icon {
            padding: 10px;
            font-size: 28px;
            color: #85858a;
            border-right: 2px solid #dfe2ec;
        }
        
        .contact-txt {
            width: 100%;
            margin: 0 10px;
            font-size: 15px;
            color: #84848e;
            font-weight: 500;
            text-align: justify;
            text-decoration: none;
        }
        
        .contact-txt>* {
            font-size: 15px;
            color: #84848e;
            font-weight: 500;
            text-align: justify;
            text-decoration: none;
        }
        /* EXPERIENCE */
        
        .experien-header {
            transition: all 1s;
            display: flex;
            flex-flow: row nowrap;
            width: 100%;
            padding: 1px;
            margin-bottom: 2px;
            align-items: center;
            justify-content: flex-start;
        }
        
        .experien-icon {
            height: 100%;
            padding: 0 12px;
            font-size: 16px;
            color: #85858a;
            justify-content: end;
            display: flex;
            border-right: 3px solid #6861e2;
            position: relative;
        }
        
        .experien-icon::after {
            content: '';
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background: #6962e2;
            box-shadow: 0 0 10px #6962e2a1;
            position: absolute;
            top: -5px;
            right: -9.5px;
        }
        
        .experien-txt {
            width: 100%;
            margin: 5px 10px;
            font-size: 15px;
            color: #84848e;
            font-weight: 500;
            text-align: justify;
            text-decoration: none;
        }
        
        .experien-txt>h2 {
            font-size: 17px;
            text-align: left;
            color: #000000;
            font-weight: 600;
            margin-bottom: 15px;
        }
        
        .experien-txt>p {
            font-size: 15px;
            line-height: 18px;
            color: #85858a;
            text-align: start;
            margin-bottom: 25px;
        }
        
        .anime {
            animation: anime1 forwards linear 1s;
        }
        
        @keyframes anime1 {
            0% {
                transform: translate(-10px, -10px);
                opacity: 0;
            }
            50% {
                transform: translate(-5px, -5px);
                opacity: .5;
            }
            100% {
                transform: translate(0, 0);
                opacity: 1;
            }
        }
        /* anime 2 */
        
        .anime2 {
            animation: anime2 forwards linear 1s;
        }
        
        @keyframes anime2 {
            0% {
                transform: translate(0, 10px);
                opacity: 0;
            }
            50% {
                transform: translate(0, 5px);
                opacity: .5;
            }
            100% {
                transform: translate(0, 0);
                opacity: 1;
            }
        }
        /* anime 3 */
        
        .anime3 {
            animation: anime3 forwards linear 1s;
        }
        
        @keyframes anime3 {
            0% {
                transform: scale(0.5);
                opacity: 0;
            }
            100% {
                transform: scale(1);
                opacity: 1;
            }
        }